<template>
  <div class="register">
    <div class="container">
      <img src="@/assets/login/logo.png" alt="">
      <ul class="main">
        <li>
          <jun-input v-model="form.username"
            color="#e4e4e4"
            icon-class="user"
            placeholder="请输入用户名"
            clear='true'>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="form.phone"
            color="#e4e4e4"
            icon-class="phone"
            placeholder="请输入手机号码"
            clear='true'>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="form.password"
            color="#e4e4e4"
            type="password"
            icon-class="password"
            placeholder="请输入密码"
            clear='true'>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="form.restpassword"
            color="#e4e4e4"
            type="password"
            icon-class="password"
            placeholder="请再次确认密码"
            clear='true'>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="form.invcode"
            color="#e4e4e4"
            icon-class="invite"
            placeholder="请输入邀请码"
            :disabled="disabled"
            clear>
          </jun-input>
        </li>
        <li>
          <jun-input v-model="imgCode"
            color="#e4e4e4"
            icon-class="auth"
            placeholder="请输入图形验证码">
          </jun-input>
          <img-code type='1'/>
        </li>
        <li>
          <jun-input v-model="form.smscode"
            color="#e4e4e4"
            icon-class="auth"
            placeholder="请输入短信验证码">
          </jun-input>
          <send-code :data="{ type: 1, phone: form.phone, code: imgCode }"/>
        </li>
      </ul>
      <button class="regBtn" @click="register">立即注册</button>
      <router-link to="/" tag='p'>已经注册？立即登录</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import JunInput from '@/components/JunInput'
import imgCode from '@/components/imgCode'
import sendCode from '@/components/sendCode'
import { inviteCode } from '@/utils'
import { validatePhone, validatePassword } from '@/utils/validate'

export default {
  name: 'register',
  components: {
    JunInput,
    imgCode,
    sendCode
  },
  data () {
    return {
      imgCode: '',
      disabled: inviteCode().status,
      form: {
        username: '',
        phone: '',
        password: '',
        restpassword: '',
        invcode: inviteCode().inviteCode,
        smscode: ''
      },
      validate: {
        username: '用户名不能为空',
        phone: '请输入正确的手机号码',
        smscode: '请输入短信验证码',
        invcode: '请输入邀请码',
        password: '请输入密码',
        restpassword: '请输入确认密码'
      }
    }
  },
  created () {
  },
  methods: {
    async verify () {
      for (var key in this.form) {
        if (!this.form[key]) {
          this.$toast({ message: this.validate[key], position: 'top' })
          return false
        }
      }
    },
    async register () {
      const condition = await this.verify()
      if (condition !== false) {
        if (!validatePhone(this.form.phone)) {
          this.$toast({ message: '请输入有效的手机号码！', position: 'top' })
          return false
        } else if (!validatePassword(this.form.password)) {
          this.$toast({ message: '密码格式不正确！', position: 'top' })
          return false
        } else if (this.form.password !== this.form.restpassword) {
          this.$toast({ message: '两次密码不一致', position: 'top' })
          return false
        } else {
          this.request({
            url: '/user',
            method: 'post',
            data: this.form
          }).then(res => {
            this.$toast(res.message)
            res.code === 1 && this.$router.push('/')
          })
        }
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
