import requset from '@/utils/request'

let random = ''
export function verify ({ phone, code, type }) {
  console.log(random)
  // type 10:普通验证码,20:注册验证码,30:修改密码验证码
  return requset({
    url: '/ver-sms',
    method: 'post',
    data: {
      phone,
      sms_imgcode: code,
      type,
      keys: random
    }
  })
}

export function getCode (type) {
  random = parseInt(Math.random() * 100000000)
  // type 10:普通验证码,20:注册验证码,30:修改密码验证码
  return `${process.env.VUE_APP_BASE_API}ver-code?type=0&keys=?type=${type}&keys=${random}`
}
