<template>
    <img class="get-code" @click="change" :style="{ background: bg }" :src="imgCode" alt="">
</template>

<script>
// @ is an alias to /src
import { getCode } from '@/api/verifyCode'

export default {
  name: 'home',
  components: {
  },
  data () {
    return {
      imgCode: ''
    }
  },
  props: {
    type: [String, Number],
    bg: {
      type: String,
      default: 'white'
    }
  },
  created () {
    this.imgCode = getCode(this.type)
  },
  methods: {
    change () {
      this.imgCode = getCode(this.type)
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="scss">
  @import '@/style/mixin.scss';
  .get-code {
    @include wh(1.4rem, .6rem);
  }
</style>
