<template>
  <button
    class="sendBtn"
    ref="sendCode"
    @click="send"
    :style="{'border-left': `solid 0.01rem ${color}`, color}">发送验证码</button>
</template>

<script>
// @ is an alias to /src
import { timer } from '@/utils'
import { verify } from '@/api/verifyCode'

export default {
  name: 'sendCode',
  components: {
  },
  data () {
    return {
      imgCode: ''
    }
  },
  model: {
    prop: 'data',
    event: 'change'
  },
  props: {
    type: [String, Number],
    color: {
      type: String,
      default: '#5dd1ff'
    },
    count: {
      type: Number,
      default: 60
    },
    /**
     * type integer($16)
     * 10:普通验证码,20:注册验证码,30:修改密码验证码
     * code string($16)
     * 图像验证码
     * phone string($32)
     * 接口短信的手机号码
     */
    data: [Object]
  },
  created () {
  },
  methods: {
    // send () {
    //   this.$emit('send')
    //   // verify(this.data).then(res => {
    //   // })
    // },
    message (res) {
      this.$toast({
        position: 'top',
        message: res.message
      })
    },
    send () {
      if (!this.data.phone) {
        this.$toast({
          position: 'top',
          message: '手机号不能为空'
        })
      } else if (!this.data.code) {
        this.$toast({
          position: 'top',
          message: '图形验证码不能为空'
        })
      } else {
        verify(this.data).then(res => {
          if (res.code === 1) {
            this.$emit('send')
            timer()
          }
          this.message(res)
        })
      }
    }
  },
  mounted () {
  }
}
</script>

<style scoped lang="scss">
  @import '@/style/mixin.scss';
  .sendBtn {
    @include space(padding-left, 0.1rem);
    @include sc(.25rem, #5dd1ff);
    min-width: 1.5rem;
    border-left: solid 0.01rem #5dd1ff;
    z-index: 1;
    background: none;
  }
</style>
